import axios from 'axios'
import globalFunction from '@/globalFunction.js'
import router from '@/router/router'
import { MessageBox, Message } from 'element-ui';
import {stringify} from "qs";

// 
const loginTip = function() {
  MessageBox.alert('', '', {
    confirmButtonText: '',
    callback: () => {
      router.push({
        path: '/login',
        query: { Rurl: router.currentRoute.fullPath }  //  urllogin
      })
    }
  });
}

// 
axios.defaults.timeout = 10000 * 5

//  URL
axios.defaults.baseURL = '/api'

// POST 
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'

// cookie
axios.defaults.withCredentials = true

// 
axios.interceptors.request.use(
  (config) => {
    config.headers['token'] = globalFunction.getCookies('token')
    return config
  },
  (error) => {
    console.log(error)
    return Promise.reject(error)
  }
)

// 
axios.interceptors.response.use(
  (response) => {
    if (response.status === 200) {
      return Promise.resolve(response)
    }
  },
  // 200
  (error) => {
    if (error.response.status) {
      console.log(error.response)
      switch(error.response.status) {
        case 404:
          Message.warning('')
          break
        case 401:
          loginTip()
          break
        case 500:
          Message.error('')
          break
        case 502:
          Message.error('')
          break
        default:
          return Promise.reject(error.response)
      }
    }
  }
)

/**
 *  get  GET 
 * @param {string} url url
 * @param {object} params 
 * @returns {Promise}
 */
export function get(url, params) {
  return new Promise((resolve, reject) => {
    axios
      .get(url, {
        params: params,
        paramsSerializer: params => {
          return stringify(params, { indices: false })
        }
      })
      .then((res) => {
        resolve(res.data)
      })
      .catch((err) => {
        reject(err.data)
      })
  })
}
/**
 *  post  POST 
 * @param {string} url url
 * @param {object} data 
 * @param {boolean | undefined} info  FormData 
 * @returns {Promise}
 */
export function post(url, data = {}, info) {
  return new Promise((resolve, reject) => {
    let newData = data
    if (info) {
      //  formData
      newData = new FormData()
      for (let i in data) {
        newData.append(i, data[i])
      }
    }
    axios
      .post(url, newData)
      .then((res) => {
        resolve(res.data)
      })
      .catch((err) => {
        reject(err.data)
      })
  })
}

/**
 *  put  PUT 
 * @param {string} url url
 * @param {object} params 
 * @returns {Promise}
 */
export function put(url, params = {}) {
  return new Promise((resolve, reject) => {
    axios.put(url, params).then(
      (res) => {
        resolve(res.data)
      },
      (err) => {
        reject(err.data)
      }
    )
  })
}

/**
 *  axiosDelete  DELETE 
 * @param {string} url url
 * @param {object} params 
 * @returns {Promise}
 */
export function axiosDelete(url, params = {}) {
  return new Promise((resolve, reject) => {
    axios
      .delete(url, params)
      .then((res) => {
        resolve(res.data)
      })
      .catch((err) => {
        reject(err.data)
      })
  })
}
